



































































import {
  computed, defineComponent, PropType, Ref,
} from '@vue/composition-api';
import {
  ColorBoxEditorControl,
  ComboBoxEditorControl,
} from '@/app/editor/client/view/component/control';
import {
  BackgroundType, GeneralDesignDefaults,
  GeneralDesignSettings,
} from '@/app/shared/client/type/settings/general-settings';
import { Color } from '@/app/shared/client/type/color';
import { GlobalDesign } from '@/app/shared/client/type/tile/global';
import { calculateActualColorDefault } from '@/app/editor/client/logic/defaults';
import useI18N from '@/app/shared/client/logic/i18n';

function useHeaderClickHandler(emit: (event: string) => void): () => void {
  function headerClickHandler(): void {
    emit('change-active');
  }
  return headerClickHandler;
}

function useBackgroundTypeChangeHandler(
  settings: Readonly<Ref<Readonly<GeneralDesignSettings>>>,
  emit: (event: string, arg: GeneralDesignSettings) => void,
): (value?: BackgroundType) => void {
  function onBackgroundTypeChange(value?: BackgroundType): void {
    if (value === undefined) {
      const newSettings = {
        ...settings.value,
        background: undefined,
      };
      emit('editor-action', newSettings);
      return;
    }

    const newBackground = {
      ...settings.value.background,
      type: value,
    };
    const newSettings = {
      ...settings.value,
      background: newBackground,
    };
    emit('editor-action', newSettings);
  }

  return onBackgroundTypeChange;
}

function useSolidBackgroundColorChangeHandler(
  settings: Readonly<Ref<Readonly<GeneralDesignSettings>>>,
  emit: (event: string, arg: GeneralDesignSettings) => void,
): (value?: Color) => void {
  function onSolidBackgroundColorChange(value?: Color): void {
    const newSolid = {
      ...settings.value.background?.solid,
      color: value,
    };
    const newBackground = {
      ...settings.value.background,
      solid: newSolid,
    };
    const newSettings = {
      ...settings.value,
      background: newBackground,
    };
    emit('editor-action', newSettings);
  }

  return onSolidBackgroundColorChange;
}

function useGradientBackgroundFromColorChangeHandler(
  settings: Readonly<Ref<Readonly<GeneralDesignSettings>>>,
  emit: (event: string, arg: GeneralDesignSettings) => void,
): (value?: Color) => void {
  function onGradientBackgroundFromColorChange(value?: Color): void {
    const newGradient = {
      ...settings.value.background?.gradient,
      fromColor: value,
    };
    const newBackground = {
      ...settings.value.background,
      gradient: newGradient,
    };
    const newSettings = {
      ...settings.value,
      background: newBackground,
    };
    emit('editor-action', newSettings);
  }

  return onGradientBackgroundFromColorChange;
}

function useGradientBackgroundToColorChangeHandler(
  settings: Readonly<Ref<Readonly<GeneralDesignSettings>>>,
  emit: (event: string, arg: GeneralDesignSettings) => void,
): (value?: Color) => void {
  function onGradientBackgroundToColorChange(value?: Color): void {
    const newGradient = {
      ...settings.value.background?.gradient,
      toColor: value,
    };
    const newBackground = {
      ...settings.value.background,
      gradient: newGradient,
    };
    const newSettings = {
      ...settings.value,
      background: newBackground,
    };
    emit('editor-action', newSettings);
  }

  return onGradientBackgroundToColorChange;
}

function calculateBackgroundTypeChoices(
  defaultBackground: string | undefined,
  i18n: (label: string) => string,
): Array<{ key: string | undefined, label: string }> {
  const choices = [
    { key: BackgroundType.SOLID, label: i18n('CommonLabels.General.Background.SolidColor') },
    { key: BackgroundType.GRADIENT, label: i18n('CommonLabels.General.Background.Gradient') },
  ];

  const defaultBackgroundLabel = choices
    .find((choice) => defaultBackground === choice.key)
    ?.label ?? '';

  const defaultBackgroundTypeText = i18n('CommonLabels.General.Background.Default')
    .replace('{backgroundType}', defaultBackgroundLabel);

  return [
    { key: undefined, label: defaultBackgroundTypeText },
    ...choices,
  ];
}

const GeneralDesignEditor = defineComponent({
  components: {
    ComboBoxEditorControl,
    ColorBoxEditorControl,
  },
  props: {
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<GeneralDesignSettings>,
    },
    defaults: {
      required: true,
      type: Object as PropType<GeneralDesignDefaults>,
    },
    siteSettings: {
      required: false,
      default: undefined,
      type: Object as PropType<GlobalDesign>,
    },
    colorList: {
      required: true,
      type: Array as PropType<string[]>,
    },
    autoColorEnabled: {
      required: true,
      type: Boolean,
    },
    alphaColorEnabled: {
      required: true,
      type: Boolean,
    },
  },
  setup(props, { root, emit }) {
    const i18n = useI18N(root.$store);

    const settings = computed(() => ({
      background: {
        type: props.value?.background?.type,
        solid: props.value?.background?.solid,
        gradient: props.value?.background?.gradient,
      },
    }));

    const headerClickHandler = useHeaderClickHandler(emit);

    const backgroundTypeChoices = computed(
      () => calculateBackgroundTypeChoices(props.defaults.background?.type, i18n),
    );

    const backgroundType = computed({
      get: () => settings.value.background.type,
      set: useBackgroundTypeChangeHandler(settings, emit),
    });

    const defaultSolidBackgroundColor = computed(() => calculateActualColorDefault(
      props.defaults.background?.solid?.color,
      props.siteSettings,
    ));
    const solidBackgroundColor = computed({
      get: () => settings.value.background.solid?.color,
      set: useSolidBackgroundColorChangeHandler(settings, emit),
    });

    const defaultGradientBackgroundFromColor = computed(() => calculateActualColorDefault(
      props.defaults.background?.gradient?.fromColor,
      props.siteSettings,
    ));
    const gradientFromColor = computed({
      get: () => settings.value.background.gradient?.fromColor,
      set: useGradientBackgroundFromColorChangeHandler(settings, emit),
    });

    const defaultGradientBackgroundToColor = computed(() => calculateActualColorDefault(
      props.defaults.background?.gradient?.toColor,
      props.siteSettings,
    ));
    const gradientToColor = computed({
      get: () => settings.value.background.gradient?.toColor,
      set: useGradientBackgroundToColorChangeHandler(settings, emit),
    });

    return {
      i18n,
      backgroundType,
      backgroundTypeChoices,
      defaultGradientBackgroundFromColor,
      gradientFromColor,
      defaultGradientBackgroundToColor,
      gradientToColor,
      headerClickHandler,
      defaultSolidBackgroundColor,
      solidBackgroundColor,
    };
  },
});

export default GeneralDesignEditor;
